import onOpenModal from './onOpenModal';
import modal from '../templates/modal.hbs';
import { refs } from '../base/refs';

export default function onHero(e) {
  e.preventDefault();

  if (e.target.className === 'hero' || e.target.nodeName === 'UL') return; // 
  const target = e.target.closest('.filmCard');

  const myCardId = target.dataset.id; //  

  const myCard = JSON.parse(localStorage.getItem('cards')).find(el => el.id == myCardId);

  /*       */
  const arrGenresLs = JSON.parse(localStorage.getItem('genres')); //     /     

  myCard.genre_str = myCard.genre_ids
    .map(x => {
      return arrGenresLs.find(({ id }) => id === x);
    })
    .map(({ name }) => name)
    .join(', ');

  const modalMarkup = modal(myCard); //  
  refs.modalSearch.innerHTML = modalMarkup; //   

  onOpenModal(myCard);
  return;
}
